import React, { useState } from "react";
import { Helmet } from "react-helmet-async";
import { useNavigate } from "react-router-dom";
import { useCreateArticle } from "features/articles/hooks/useCreateArticle";
import "./styles.css";
import { prompt } from "./prompt";

const CreateArticlePage = () => {
    const [jsonInput, setJsonInput] = useState("");
    const [isValid, setIsValid] = useState(true);
    const [error, setError] = useState("");
    const navigate = useNavigate();

    const { createArticle, loading } = useCreateArticle();

    const handleJsonChange = (e) => {
        const value = e.target.value;
        setJsonInput(value);

        if (value.trim()) {
            try {
                JSON.parse(value);
                setIsValid(true);
                setError("");
            } catch (err) {
                setIsValid(false);
                setError(" JSON: " + err.message);
            }
        } else {
            setIsValid(true);
            setError("");
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!jsonInput.trim() || !isValid) {
            setError("  JSON");
            return;
        }

        try {
            const articleData = JSON.parse(jsonInput);
            const result = await createArticle(articleData);

            if (result.success) {
                alert("  !");
                setJsonInput("");
                //      
                navigate(`/articles/${result.slug}`);
            } else {
                setError(result.error || "   ");
            }
        } catch (err) {
            setError(": " + err.message);
        }
    };

    const handleClear = () => {
        setJsonInput("");
        setError("");
        setIsValid(true);
    };

    const loadExample = () => {
        const example = {
            slug: "example-article",
            title: " ",
            description: "     ",
            category: "  ",
            dateDisplay: "10  2025 .",
            datePublishedISO: "2025-10-10T00:00:00+03:00",
            author: " Breaking NEWS",
            section: "",
            tags: ["", "", ""],
            hero: {
                url: "https://example.com/image.jpg",
                alt: " ",
                caption: "  ",
            },
            content: [
                {
                    type: "paragraph",
                    html: "   .",
                },
                {
                    type: "heading",
                    level: 2,
                    text: " ",
                },
            ],
        };

        setJsonInput(JSON.stringify(example, null, 2));
        setIsValid(true);
        setError("");
    };

    return (
        <>
            <Helmet>
                <title>  |  Breaking NEWS</title>
                <meta
                    name="description"
                    content="      "
                />
            </Helmet>

            <main className="main">
                <div className="container">
                    <div className="create-article-page">
                        <header className="page-header">
                            <h1 className="page-title"> </h1>
                            <p className="page-subtitle">
                                 JSON      
                                 
                            </p>
                        </header>

                        <div className="create-article-content">
                            <div className="format-info">
                                <h3>  </h3>
                                <ul>
                                    <li>     JSON</li>
                                    <li>
                                         : slug, title,
                                        description, content
                                    </li>
                                    <li>
                                         <code>slug</code>  
                                        
                                    </li>
                                    <li>
                                          " "
                                          
                                    </li>
                                </ul>
                            </div>

                            {/*   */}
                            <form onSubmit={handleSubmit} className="json-form">
                                <div className="form-header">
                                    <label
                                        htmlFor="json-input"
                                        className="form-label"
                                    >
                                        JSON  
                                    </label>
                                    <div className="form-actions">
                                        <button type="button"
                                        onClick={
                                            async () => {
                                                try {
                                                    await navigator.clipboard.writeText(prompt);
                                                    console.log('Text copied to clipboard successfully!');
                                                    alert("  !");
                                                  } catch (err) {
                                                    console.error('Failed to copy text: ', err);
                                                  }
                                            }
                                        }
                                        className="btn btn-outline"
                                        disabled={loading}>
                                              
                                        </button>
                                        <button
                                            type="button"
                                            onClick={loadExample}
                                            className="btn btn-outline"
                                            disabled={loading}
                                        >
                                              
                                        </button>
                                        <button
                                            type="button"
                                            onClick={handleClear}
                                            className="btn btn-outline"
                                            disabled={loading}
                                        >
                                             
                                        </button>
                                    </div>
                                </div>

                                <textarea
                                    id="json-input"
                                    className={`json-input ${
                                        !isValid ? "error" : ""
                                    }`}
                                    value={jsonInput}
                                    onChange={handleJsonChange}
                                    placeholder='{"slug": "my-article", "title": " ", ...}'
                                    rows={20}
                                    disabled={loading}
                                />

                                {error && (
                                    <div className="error-message">
                                         {error}
                                    </div>
                                )}

                                {isValid && jsonInput.trim() && (
                                    <div className="success-message">
                                         JSON 
                                    </div>
                                )}

                                <div className="submit-section">
                                    <button
                                        type="submit"
                                        className="btn btn-primary submit-btn"
                                        disabled={
                                            !jsonInput.trim() ||
                                            !isValid ||
                                            loading
                                        }
                                    >
                                        {loading
                                            ? " ..."
                                            : "  "}
                                    </button>

                                    <span className="char-count">
                                        : {jsonInput.length}
                                    </span>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </main>
        </>
    );
};

export default CreateArticlePage;
