/**
 *  ISO     
 * @param {string} dateISO -    ISO string
 * @param {string} format -  : 'full' | 'short' | 'relative'
 * @returns {string} -  
 */
export const formatDate = (dateISO, format = 'full') => {
    if (!dateISO) return '';
    
    try {
      const date = new Date(dateISO);
      const now = new Date();
      
      if (isNaN(date.getTime())) {
        console.warn('Invalid date:', dateISO);
        return '';
      }
      
      const day = date.getDate();
      const month = date.getMonth();
      const year = date.getFullYear();
      const hours = date.getHours().toString().padStart(2, '0');
      const minutes = date.getMinutes().toString().padStart(2, '0');
      
      const monthNames = [
        '', '', '', '', '', '',
        '', '', '', '', '', ''
      ];
      
      const monthNamesShort = [
        '', '', '', '', '', '',
        '', '', '', '', '', ''
      ];
  
      switch (format) {
        case 'full':
          return `${day} ${monthNames[month]} ${year}`;
          
        case 'with-time':
          return `${day} ${monthNames[month]} ${year}  ${hours}:${minutes}`;
          
        case 'short':
          return `${day} ${monthNamesShort[month]} ${year}`;
          
        case 'relative':
          return getRelativeTime(date, now);
          
        case 'month-year':
          return `${monthNames[month]} ${year}`;
          
        default:
          return `${day} ${monthNames[month]} ${year}`;
      }
    } catch (error) {
      console.error('Error formatting date:', error);
      return '';
    }
  };
  
  /**
   *    (, "2  ")
   */
  const getRelativeTime = (date, now) => {
    const diffMs = now - date;
    const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
    const diffMinutes = Math.floor(diffMs / (1000 * 60));
    
    if (diffMinutes < 1) {
      return ' ';
    } else if (diffMinutes < 60) {
      return `${diffMinutes} ${getNoun(diffMinutes, '', '', '')} `;
    } else if (diffHours < 24) {
      return `${diffHours} ${getNoun(diffHours, '', '', '')} `;
    } else if (diffDays === 1) {
      return '';
    } else if (diffDays === 2) {
      return '';
    } else if (diffDays < 7) {
      return `${diffDays} ${getNoun(diffDays, '', '', '')} `;
    } else {
      return formatDate(date.toISOString(), 'full');
    }
  };
  
  /**
   *    
   */
  const getNoun = (number, one, two, five) => {
    let n = Math.abs(number);
    n %= 100;
    if (n >= 5 && n <= 20) {
      return five;
    }
    n %= 10;
    if (n === 1) {
      return one;
    }
    if (n >= 2 && n <= 4) {
      return two;
    }
    return five;
  };